.page{
    
    margin-top: 50vh; /* poussé de la moitié de hauteur de viewport */
    transform: translateY(-50%); /* tiré de la moitié de sa propre hauteur */
    
    display: flex;
    justify-content: center;

}

.logo {
    position: absolute;
    top: 5%;
    left: 5%;
}

@font-face {
    font-family: "Futura";
    src: local("Futura"),
     url("./FuturaMedium/Futura Medium.otf") format("truetype");
    font-weight: bold;
    }

.formlogin{

    width: 500px;
    height: 350px;

}

.hautform{

    border-bottom: #4088BF solid 4px;
    
    display: flex;
    justify-content: center;
    align-items: center;

}

.containerform{
    margin-top: 3%;
    border: 1px solid #D9DCDD;
    border-radius: 10px;
    padding: 5% 3%;

    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;

}


    h1 {
font-family: "Futura"; 
font-style: normal;
font-weight: 500;
font-size: 40px;
margin-bottom: 1%;
    }

.midform{

    display: flex;
    flex-direction: column;


}

label {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    color: #1C1610;
    margin-bottom: 2%;
}

input {
    border: 5px solid #D9DCDD;
    border-radius: 10px;
    padding: 3%;
    

    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    margin-bottom: 4%;
    
}

.basform{
    width: 100%;
    padding: 2% 0;
    margin-top: 4%;
    font-family: "Futura";
    font-weight: 500;
    font-size: 40px;
    background: rgba(217, 220, 221, 0.8);
    border: 4px solid #4088BF;
    border-radius: 10px;
    cursor: pointer;
    transition-duration: 0.4s;

    
    

}

button:hover {
    color: white;
    background: rgba(64, 136, 191, 0.8);
}

